.header {
    flex-grow: 1;
    flex-basis: 0;
    display: grid;
    grid-template-columns: 1fr 1fr 2fr 1fr 1fr;
    gap: 1px;
    padding-bottom: 1px;
}
.header li {
    background-color: black;
    color: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.header li:nth-child(3) {
    font-size: 3rem;
}
li {
    list-style-type: none;
}