.content {
    flex-grow: 8;
    flex-basis: 0;
    width: 100%;
}
.content div {
    text-align: center;
    font-size: 1rem;
}
.geschichte {
    height: 100%;
    overflow: hidden;
}
.geschichte div {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 30px;
}
.geschichte > div > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.geschichte > div > div > p {
    padding: 5px;
    border: 1px solid black;
    cursor: pointer;
    box-shadow: 2px 2px 2px #aaaaaa;
}
.geschichte > div > div > p:hover {
    background-color: black;
    color: white;
}
.geschichte img{
    width: 50px;
    height: 50px;
    box-shadow: 2px 2px 2px #aaaaaa;
}
